import { css } from 'styled-components';

import MoniqaMediumHeadingOTF from '@fonts/Moniqa-MediumHeading.otf';
import MoniqaMediumParagraphOTF from '@fonts/Moniqa-MediumParagraph.otf';

const moniqaRegularWeights = {
  400: [MoniqaMediumParagraphOTF],
  500: [MoniqaMediumHeadingOTF],
};

const moniqa = {
  name: 'Moniqa',
  normal: moniqaRegularWeights,
};

const createFontFaces = (family, style = 'normal') => {
  let styles = '';

  for (const [weight, formats] of Object.entries(family[style])) {
    const otf = formats[0];

    styles += `
      @font-face {
        font-family: '${family.name}';
        src: url(${otf}) format('otf'),
        font-weight: ${weight};
        font-style: ${style};
        font-display: auto;
      }
    `;
  }

  return styles;
};

const moniqaRegular = createFontFaces(moniqa);

const Fonts = css`
  ${moniqaRegular}
`;

export default Fonts;
